.bitacora-container {
    background-color: var(--claro);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    padding: 1rem;
    width: auto;
    height: auto;
    border-radius: 1rem;
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);
}

.bitacora-table-encabezado {
    background-color: var(--blanco);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: auto;
    transition: .3s ease-in-out;
    border-radius: .5rem;
    margin: .3rem;
}

.bitacora-table-encabezado:hover {
    background-color: var(--blanco);
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);

}

.bitacora-titulos-tabla {
    background-color: var(--primario);
}

.bitacora-titulos-tabla:hover {
    background-color: var(--primario);
    filter: saturate(150%);
}

.bitacora-column {
    font-size: 1rem;
    width: 12rem;
    text-align: center;
}

.bitacora-encabezado {
    font-weight: bold;
    font-size: 1.2rem;
    color: var(--blanco);
    padding: .5rem 0rem;
}

.bitacora-btnCheckdetails {
    width: 3rem;
    height: 3rem;
    padding: none;
    border-radius: .5rem;
    transition: .3s ease-in-out;
    background-color: var(--blanco);
    border: none;
    cursor: pointer;
}

.bitacora-ico {
    width: 100%;
    height: auto;
    background-color: var(--primario);
    padding: .5rem;
    padding-left: .7rem;
    border-radius: .5rem;
    transition: .3s ease-in-out;
}

.bitacora-btnCheckdetails:hover .bitacora-ico {
    background-color: var(--primario);
    filter: saturate(550%);
}

.grafico-linea-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 80%;
    height: auto;
    /* Añade margen superior e inferior para centrar */
}

.grafico-linea {
    margin: 0rem 2rem;
    width: 30%;
    /* Ajusta el ancho según tus necesidades */
    height: auto;
    padding: 1rem;
    /* Añade un poco de espacio interno */
    background-color: var(--blanco);
    border-radius: 0.5rem;
    border: 2px solid var(--gris);
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);
}

/* Estilos específicos para los gráficos */
.grafico-linea canvas {
    width: 100%;
    /* Asegura que el canvas del gráfico ocupe todo el espacio disponible */
    height: auto;
}

/* Estilo para el contenedor de filtros */
.filtros-container {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 0;
    padding: .2rem;
    border-radius: .5rem;
}

/* Estilo para los contenedores de filtro individuales */
.filtro-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: .5rem;
}

/* Estilo para las etiquetas de los filtros */
.filtro-container label {
    font-weight: bold;
    margin-bottom: 5px;
}

/* Estilo para los campos de entrada de los filtros */
.filtro-container input {
    padding: 5px;
    border: 1px solid var(--blanco);
    border-radius: 5px;
    font-size: 16px;
    margin-left: .5rem;
    border: 1px solid var(--gris);
}

/* Estilo para los campos de entrada de los filtros cuando están enfocados */
.filtro-container input:focus {
    outline: none;
    border-color: #007bff;
    /* Color de resaltado al enfocar */
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
    /* Sombra al enfocar */
}

