.pareja-form-container {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px rgba(250, 250, 250, 0.5);
    border-radius: .5rem;
    background-color: var(--primario);
    border: 1px solid var(--blanco);
    width: 100%;
    height: auto;
    margin-bottom: 1rem;
}

.pareja-registro-guardado-exitosamente {
    color: var(--blanco);
    font-size: 1.4rem;
    font-weight: 400;
    width: 30.3rem;
    margin-bottom: 1rem;
}

.pareja-img-correcto {
    width: 2rem;
    height: auto;
    padding-left: .5rem;
}

.pareja-form-label {
    color: var(--blanco);
}

.pareja-form-formlarios {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.pareja-form-inputs-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.pareja-form-input-names {
    padding: .5rem 1rem;
    width: calc(40% - 1rem); /* 50% width for 2 inputs per column with some spacing */
    font-size: 1rem;
    margin: .5rem;
    text-align: left;
    border-radius: .5rem;
    box-shadow: 0 2px 4px rgba(100, 100, 100, 0.5);
    border: none;
    outline: none;
    background-color: var(--blanco);
    margin-bottom: 1rem;
    border: 1px solid var(--gris);
}


.name-form-inputs-label {
    font-size: 1.4rem;
    color: var(--blanco);
    margin-top: 1rem;
    font-weight: bold;
}

.name-form-inputs-par {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
}

.name-form-inputs-par-opc {
    font-size: 1.2rem;
    color: var(--blanco);
    margin: .5rem;
}

.name-form-button-pareja {
    background-color: var(--claro);
    color: var(--primario);
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: .3s ease-in-out;
    margin: .5rem;
    margin-top: 1rem;
}

.name-form-button-pareja:hover {
    background-color: var(--blanco);
    filter: saturate(150%);
}

h4 {
    margin-top: 0rem;
    margin-bottom: 1rem;
    font-weight: 600;
}