.crud-container {
    text-align: center;
}

.crud-fomularios {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
}

.crud-formularios-menu {
    width: 70%;
    height: auto;
    display: flex;
    flex-direction: row;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    background-color: var(--claro);
    padding: .5rem;
    margin: .5rem;
    border-radius: 1rem;
}

/* Estilos para los botones del CRUD */
.crud-formularios-menu button {
    width: 18rem;
    height: auto;
    background-color: var(--primario);
    color: var(--blanco);
    padding: .5rem 1rem;
    margin: 0.5rem;
    border: none;
    border-radius: 0.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    font-weight: bold;
}

.crud-formularios-menu button:hover {
    filter: saturate(250%);
}