.alert-window-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

.alert-window-content {
    background-color: var(--blanco);
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    text-align: center;
}

.alert-window-message {
    font-size: 1.2rem;
    margin-bottom: 2rem;
}

.alert-window-button {
    padding: .8rem 1.5rem;
    background-color: var(--primario);
    color: var(--blanco);
    border: none;
    border-radius: .5rem;
    cursor: pointer;
    transition: .3s ease-in-out;
}

.alert-window-button:hover {
    filter: saturate(250%);
}