.main {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.main-container {
    background-color: var(--claro);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    padding-right: 1.5rem;
    margin-bottom: 1rem;
    width: auto;
    height: auto;
    border-radius: 1rem;
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);
}

.main-table-encabezado {
    background-color: var(--blanco);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: auto;
    transition: .3s ease-in-out;
    border-radius: .5rem;
    margin: .3rem;
}

.main-table-encabezado:hover {
    background-color: var(--blanco);
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);

}

.main-titulos-tabla {
    background-color: var(--primario);
}

.main-titulos-tabla:hover {
    background-color: var(--primario);
    filter: saturate(150%);
}

.main-column {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 1.2rem;
    width: 18rem;
    text-align: center;
    padding: .2rem .2rem;
}

.main-encabezado {
    width: 18rem;
    font-weight: bold;
    font-size: 1.4rem;
    color: var(--blanco);
    padding: .5rem .2rem;
}

.main-btnCheckout {
    width: auto;
    height: auto;
    padding: none;
    transition: .3s ease-in-out;
    background-color: var(--blanco);
    border: none;
    cursor: pointer;
}

.main-btnCheckout:hover .main-ico {
    filter: saturate(500%);
}

.main-ico {
    height: auto;
    width: 2rem;
    background-color: var(--secundario);
    padding: .5rem;
    padding-left: .7rem;
    border-radius: .5rem;
    transition: .3s ease-in-out;
}

.grafico-linea-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 80%;
    height: auto;
    margin: 2rem auto;
    /* Añade margen superior e inferior para centrar */
}

.grafico-linea {
    margin: 0rem 2rem;
    width: 35%;
    /* Ajusta el ancho según tus necesidades */
    height: auto;
    padding: 1rem;
    /* Añade un poco de espacio interno */
    background-color: var(--blanco);
    border-radius: 0.5rem;
    border: 2px solid var(--gris);
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);
}

/* Estilos específicos para los gráficos */
.grafico-linea canvas {
    width: 100%;
    /* Asegura que el canvas del gráfico ocupe todo el espacio disponible */
    height: auto;
}