.view-registros {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    width: auto;
    height: auto;
    padding-bottom: 1rem;
}

/* Estilos adicionales según sea necesario */

.view-registros-tabla-visitas {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    background-color: var(--claro);
    padding: 1rem;
    border-radius: 1rem;
    width: 80%;
    height: auto;
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);

}

.view-registros-tabla-encabezado {
    background-color: var(--blanco);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: auto;
    transition: .3s ease-in-out;
    padding: .4rem;
    border-radius: .5rem;
    margin: .2rem;
    text-align: center;
}

.view-registros-tabla-encabezado:hover {
    background-color: var(--blanco);
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);
}

.view-registros-titulos-tabla {
    background-color: var(--primario);
}

.view-registros-titulos-tabla:hover {
    background-color: var(--primario);
    filter: saturate(150%);
}

.view-registros-encabezado {
    width: 20rem;
    height: auto;
    text-align: center;
    color: var(--blanco);
    font-size: 1.2rem;
    font-weight: bold;
    padding: .2rem .8rem;
}

.view-registros-column {
    width: 20rem;
    height: auto;
    text-align: center;
    font-size: 1rem;
    padding: .5rem 1rem;
}