.delete-registros {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    width: auto;
    height: auto;
    margin-bottom: 1rem;
}

/* Estilos adicionales según sea necesario */

.delete-registros-tabla-visitas {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    background-color: var(--claro);
    padding: 1rem;
    border-radius: 1rem;
    width: 90%;
    height: auto;
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);
}

.delete-registros-tabla-encabezado {
    background-color: var(--blanco);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: auto;
    transition: .3s ease-in-out;
    padding: .2rem;
    border-radius: .5rem;
    margin: .2rem;
    text-align: center;
}

.delete-registros-tabla-encabezado:hover {
    background-color: var(--blanco);
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);
}

.delete-registros-titulos-tabla {
    background-color: var(--primario);
}

.delete-registros-titulos-tabla:hover {
    background-color: var(--primario);
    filter: saturate(150%);
}

.delete-registros-encabezado {
    width: 15rem;
    height: auto;
    text-align: center;
    color: var(--blanco);
    font-size: 1.3rem;
    font-weight: bold;
    padding: .5rem .5rem;
}

.delete-registros-column {
    width: 18rem;
    height: auto;
    text-align: center;
    font-size: 1.1rem;
    padding: .0rem .5rem;
}

.delete-main-btn-delete {
    width: 18rem;
    height: auto;
    padding: none;
    transition: .3s ease-in-out;
    background-color: var(--blanco);
    border: none;
    cursor: pointer;
}

.delete-main-btn-delete:hover .delete-main-ico {
    filter: saturate(500%);
}

.delete-main-ico {
    width: auto;
    height: 2.5rem;
    background-color: var(--secundario);
    padding: .3rem;
    border-radius: .5rem;
    transition: .3s ease-in-out;
}