.registro-detalles {
    background-color: var(--primario);
    padding: 1rem;
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);
    border-radius: .5rem;
    width: 20rem;
    height: auto;
    position: fixed;
    /* Establece la posición fija */
    top: 50%;
    /* Centra verticalmente */
    left: 50%;
    /* Centra horizontalmente */
    transform: translate(-50%, -50%);
    /* Centra en el centro de la pantalla */
    z-index: 999;
    /* Coloca la tarjeta en la parte superior */
    border: 1px solid var(--blanco);
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.registro-detalles h2 {
    text-align: center;
    color: var(--blanco);
    margin:1rem;
    font-size: 1.5rem;
}

.detalle {
    color: var(--blanco);
    margin-bottom: 1.2rem;
}

.detalle p {
    color: var(--blanco);
    margin: 0;
    font-size: 1rem;
}

.familiares h3 {
    color: var(--blanco);
    font-size: 1.4rem;
    margin-top: 2rem;
    margin: 1rem;
}

.familiar {
    color: var(--blanco);
    margin: 1rem 0;
}

.familiar p {
    color: var(--blanco);
    margin: 0rem 1rem;
}

.cerrar-detalles {
    background-color: var(--secundario);
    color: var(--blanco);
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: .3s ease-in-out;
    margin: .1rem;
    text-align: center;
}

.cerrar-detalles:hover {
    filter: saturate(250%);
}
