.confirm-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-content {
    background-color: var(--blanco);
    padding: 3rem;
    border-radius: .5rem;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.message {
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

.buttons {
    display: flex;
    justify-content: center;
}

.confirm-btn,
.cancel-btn {
    margin: 0rem 1rem;
    padding: 1rem 2rem;
    border: none;
    cursor: pointer;
    border-radius: .5rem;
}

.confirm-btn {
    padding: .8rem 1.5rem;
    background-color: var(--primario);
    color: var(--blanco);
    border: none;
    border-radius: .5rem;
    cursor: pointer;
    transition: .3s ease-in-out;
}

.confirm-btn:hover {
    filter: saturate(250%);
}

.cancel-btn {
    padding: .8rem 1.5rem;
    background-color: var(--secundario);
    color: var(--blanco);
    border: none;
    border-radius: .5rem;
    cursor: pointer;
    transition: .3s ease-in-out;
}

.cancel-btn:hover {
    filter: saturate(250%);
}