.ingreso-container {
    text-align: center;
}

.registro-fomularios {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
}

.ingreso-tabla-visitas {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    background-color: var(--claro);
    padding: 2rem;
    margin: 1rem;
    border-radius: 1rem;
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);
}

.ingreso-tabla-encabezado {
    background-color: var(--blanco);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: auto;
    transition: .3s ease-in-out;
    padding: .2rem;
    border-radius: .5rem;
    margin: .2rem;
}

.ingreso-tabla-encabezado:hover {
    background-color: var(--blanco);
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);
}

.ingresos-titulos-tabla {
    background-color: var(--primario);
}

.ingresos-titulos-tabla:hover {
    background-color: var(--primario);
    filter: saturate(150%);
}

/*-----------------------*/

.ingreso-btnCheckout {
    border: none;
    cursor: pointer;
    color: var(--blanco);
    content: "No";
    margin: .5rem;
    width: 2rem;
    height: 2rem;
    border-radius: .5rem;
}

/*-----------------------*/
.ingreso-input-button {
    padding: .5rem 1rem;
    background-color: var(--primario);
    color: var(--blanco);
    border: none;
    cursor: pointer;
    border-radius: .5rem;
    font-size: 1.2rem;
    transition: .3s ease-in-out;
    margin-top: 1rem;
}

.ingreso-input-button:hover {
    filter: saturate(250%);
}