.scanner-input-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    padding: 1rem;
    box-shadow: 0 4px 8px rgba(100, 100, 100, 0.5);
    border-radius: .5rem;
    background-color: var(--claro);
    width: 50%;
    margin: 1rem;
}

.scanner-input-label {
    font-size: 1.8rem;
    font-weight: bold;
}

.scanner-input-input {
    padding: .5rem 1rem;
    width: 30rem;
    font-size: 1.2rem;
    margin: .5rem;
    text-align: center;
    border-radius: .5rem;
}

.scanner-input-button {
    padding: .5rem 1rem;
    background-color: var(--primario);
    color: var(--blanco);
    border: none;
    cursor: pointer;
    border-radius: .5rem;
    font-size: 1.2rem;
    margin: .5rem;
    transition: .3s ease-in-out;
}

.scanner-input-button:hover {
    filter: saturate(250%);
}