:root {
  --primario: rgb(25, 65, 91);
  --gris: rgb(160, 160, 160);
  --claro: rgb(235, 235, 235);
  --secundario: rgb(255, 105, 105);
  --blanco: rgb(255, 255, 255);
  --negro: rgb(0, 0, 0);
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

@font-face {
  font-family: 'Gibson';
  src: url();
}
.app-main {
  width: 100%;
  height: auto;
}

body {
  margin: 0;
  padding: 0;
  position: relative;
  /* Asegura que el elemento ::before se posicione correctamente */
  background-image: url('./components/assets/svg/logo2.png'), url('./components/assets/svg/logo2.png');
  background-repeat: no-repeat;
  background-size: 30% auto;
  background-position: left, right;
  background-attachment: fixed;
  background-color: var(--blanco);
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 999;
}

.main-content {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  overflow: auto;
  width: 100%;
  height: auto;
  margin-bottom: 3%;
}

body::-webkit-scrollbar {
  width: 1rem;
  background-color: var(--claro);
  border-radius: 1rem;
}

body::-webkit-scrollbar-thumb {
  width: 1rem;
  background-color: var(--gris);
  border-radius: 1rem;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 999;
  width: 100%;
  height: 6%;
  background-image: url('./components/assets/footer.png');
  background-position: center;
  background-repeat: repeat;
  background-size: auto;
  background-color: var(--blanco);
}