.header {
    background-color: var(--primario);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.header-main-nav {
    background-color: var(--primario);
    border: none;
    cursor: pointer;
    padding: 0.5rem; /* Añade un espaciado al botón */
}

.header-img-titulo {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header-logo {
    width: auto;
    height: 100%; /* Cambia el valor a 100% para que se ajuste a la altura del botón */
    margin: 0rem 1rem;
    padding: none;
}

.header-titulo {
    font-size: 1.8rem;
    color: var(--blanco);
    margin: none;
    font-weight: 500;
}

.header-nav {
    background-color: var(--primario);
    padding: none;
    margin: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-nav-bt {
    background-color: var(--primario);
    padding: 0 1rem;
    height: 3rem; /* Cambia la altura a un valor fijo adecuado */
    border: none;
    margin: 0;
    font-size: 1.2rem;
    color: var(--blanco);
    cursor: pointer;
    font-weight: lighter;
    transition: 0.3s ease-in-out;
}

.header-nav-bt:hover {
    filter: saturate(250%);
}

.header-button {
    background-color: var(--secundario);
    color: var(--blanco);
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: 0.3s ease-in-out;
    margin-right: 1rem;
}

.header-button:hover {
    filter: saturate(250%);
}
