.login-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.login-title {
    transform: translateY(-5rem);
    font-size: 3rem;
    margin-top: 0rem;
    font-weight: 400;
    color: var(--primario);
}

.login-card {
    transform: translateY(-4rem);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--gris);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    width: 80%;
    max-width: 400px;
}

.login-logo {
    margin: .5rem;
    width: 40%;
    height: auto;
    background-image: radial-gradient(var(--blanco), transparent, );
}

.login-card h2 {
    margin-bottom: 1rem;
    color: var(--blanco);
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-group {
    margin-bottom: 1rem;
}

.form-group label {
    font-size: 1.1rem;
    display: block;
    margin-bottom: 0.5rem;
    color: var(--claro);
}

.form-group input {
    /* Mantén los estilos que deseas conservar */
    width: 100%;
    padding: 0.5rem;
    border-radius: 5px;
    font-size: 1rem;
    border: none;

    /* Anula los estilos por defecto */
    outline: none;
    /* Elimina el borde resaltado cuando se enfoca el input */
    background: none;
    /* Elimina el fondo por defecto del input */
    box-shadow: none;
    /* Elimina cualquier sombra por defecto */
    background-color: var(--claro);
    transition: .3s ease-in-out;
}

.form-group input:focus {
    background-color: var(--blanco);
}

.error-message {
    color: var(--blanco);
    font-size: .8rem;
    font-weight: 400;
    /* Agregar "rem" al tamaño de fuente */
}

.login-button {
    background-color: var(--primario);
    color: var(--blanco);
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: .3s ease-in-out;
}

.login-button:hover {
    filter: saturate(250%);
}