/* CSS para el componente UpdateRegistros */

.update-registros {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: auto;
}


.update-registros-tabla-visitas {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    background-color: var(--claro);
    padding: 1rem;
    border-radius: 1rem;
    width: 90%;
    height: auto;
    transition: .3s ease-in-out;
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);
}

.update-registros-tabla-encabezado {
    background-color: var(--blanco);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: auto;
    transition: .3s ease-in-out;
    border-radius: .5rem;
    margin: .2rem;
    text-align: center;
}

.update-registros-tabla-encabezado:hover {
    box-shadow: 0 8px 8px rgba(100, 100, 100, 0.5);
}

.update-registros-titulos-tabla {
    background-color: var(--primario);
    padding: .4rem 0rem;
}

.update-registros-titulos-tabla:hover {
    background-color: var(--primario);
    filter: saturate(150%);
}

.update-registros-encabezado {
    width: 10rem;
    height: auto;
    text-align: center;
    color: var(--blanco);
    font-size: 1rem;
    font-weight: bold;
    transition: .3s ease-in-out;
}


.update-registros-column {
    width: 10rem;
    height: auto;
    text-align: center;
    padding: .5rem .5rem;
}

.update-registros-input {

    /* Mantén los estilos que deseas conservar */
    width: 90%;
    padding: 0.5rem;
    border-radius: 5px;
    font-size: 1rem;
    border: 1px solid var(--claro);
    outline: none;
    background-color: var(--blanco);
    transition: .3s ease-in-out;
}

.update-checkbox-input {
    width: 1.5rem;
    height: 1.5rem;
    margin: .5rem;
    text-align: left;
    border-radius: .5rem;
    box-shadow: 0 2px 4px rgba(100, 100, 100, 0.5);
    border: none;
    outline: none;
    background-color: var(--blanco);
    border: 1px solid var(--gris);
}

/* Botón de submit */
.update-submit-button {
    margin: 1rem;
    display: block;
    background-color: var(--primario);
    color: var(--blanco);
    padding: .8rem 1.2rem;
    text-align: center;
    border: none;
    border-radius: .5rem;
    cursor: pointer;
    transition: .3s ease-in-out;
}

.update-submit-button:hover {
    filter: saturate(250%);
}