.create-registro {
    text-align: center;
    padding: 20px;
}

.names-form-container {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    border-radius: .5rem;
    width: 100%;
    margin-bottom: 1rem;
}

.names-form-pareja-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 30%;
    height: auto;
}

.names-form-familiares-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 45%;
    height: auto;
}

/* Estilos adicionales según sea necesario */

.names-form-button-rest {
    width: 15rem;
    background-color: var(--secundario);
    color: var(--blanco);
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: .3s ease-in-out;
    margin: .1rem;
}

.names-form-button-resr:hover {
    filter: saturate(150%);
}

.names-form-button-add {
    width: 15rem;
    background-color: var(--primario);
    color: var(--blanco);
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: .3s ease-in-out;
    margin: .1rem;
}

.names-form-button-add:hover {
    filter: saturate(250%);
}

.names-form-pareja-exists {
    font-size: 1rem;
    color: var(--blanco);
    padding: .5rem 1rem;
    background-color: var(--secundario);
    border-radius: .5rem;
}