.familiar-form-container {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 4px 8px rgba(250, 250, 250, 0.5);
    border-radius: .5rem;
    background-color: var(--claro);
    border: 1px solid var(--blanco);
    width: 100%;
    height: auto;
    margin-bottom: 1rem;
}

.familiar-registro-guardado-exitosamente {
    color: var(--negro);
    font-size: 1.4rem;
    font-weight: 400;
    width: 30.3rem;
    margin-bottom: 1rem;
}

.familiar-img-correcto {
    width: 2rem;
    height: auto;
    padding-left: .5rem;
}

.familiar-form-label {
    color: var(--primario);
}

.familiar-form-formlarios {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.familiar-form-inputs-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.familiar-form-name-input-names {
    padding: .5rem 1rem;
    width: calc(20% - 1rem);
    font-size: .8rem;
    margin: .2rem;
    text-align: left;
    border-radius: .5rem;
    box-shadow: 0 2px 4px rgba(100, 100, 100, 0.5);
    border: none;
    outline: none;
    background-color: var(--blanco);
    margin-bottom: 1rem;
    border: 1px solid var(--gris);
}

.familiar-name-form-inputs-label {
    font-size: 1.2rem;
    color: var(--primario);
    font-weight: bold;
}

.name-form-inputs-fam {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
}

.name-form-inputs-fam-opc {
    font-size: 1rem;
    color: var(--negro);
    margin: 0rem 1rem;
}

.name-form-button-familiar {
    background-color: var(--primario);
    color: var(--blanco);
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: .3s ease-in-out;
    margin: .5rem;
    margin-top: 1rem;
}

.name-form-button-familiar:hover {
    filter: saturate(250%);
}